@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700;800&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Nunito:wght@400;600;700;800&display=swap');

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-size: inherit;
  font-family: inherit;
  list-style-type: none;
  border-collapse: collapse;
}
a {
  text-decoration: none;
  color: inherit;
}
button {
  border: none;
  background: none;
  cursor: pointer;
}

html,
body {
  scroll-behavior: smooth;
  color: color(gray, secondary);
  font-family: "Nunito", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  overflow-x: hidden;
}

header {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: grid;
  place-items: center;
  .message-container {
    padding: 2rem;
    color: white;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(black, 0.65);
    h1,
    h2,
    h3,
    h4,
    h5 {
      text-align: center;
    }
    h1 {
      margin-top: 2rem;
      font-size: 3rem;
      font-weight: 800;
      line-height: 125%;
      color: #fca311;
      text-transform: uppercase;
    }
    h2 {
      color: #a8dadc;
      font-size: 2rem;
      font-weight: 700;
      line-height: 125%;
      padding-bottom: 2rem;
      text-transform: uppercase;
    }
    h3 {
      font-size: 1.5rem;
      line-height: 175%;
      font-weight: 600;
      padding-bottom: 2rem;
    }
    h4 {
      font-size: 1.5rem;
      line-height: 125%;
    }
    h5 {
      font-family: 'Bebas Neue', sans-serif;
      font-size: 3.5rem;
      letter-spacing: .05rem;
      line-height: 150%;
      font-weight: 800;
      text-transform: uppercase;
    }
    span {
      line-height: 200%;
      font-size: 5rem;
    }
  }
}
